// 是否为电话号码
const isPhoneNum = phone => /^1[3456789]\d{9}$/.test(phone);
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
	data() {
		return {
			phoneFocusStatus: false, // 手机号是否获得焦点
			numFocusStatus: false, // 数量是否获得焦点
			phoneErrTip: '', // 手机号错误信息
			numErrTip: '', // 数量错误信息
			phoneVal: '', // 手机号的值
			numVal: '' ,// 数量的值
			data:'',
			seere:'',
			password: '',
			numberList: [],
			length: 6,
			type: 'number',
			passIn: false,
			phone:'',
			// sher:false
			isFocus:false,
		};
	},
	components: {
	    keyboardPackage,
	    passwordInput
	},
	onLoad(option) {
		console.log(option)
		this.data = option.aa
		this.seere = option.ff
		console.log(option.aa)
		console.log(option.ff)
		console.log(this.phoneVal)
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord()
	},
	methods: {
		/**
		 * 获得焦点触发
		 * @param {String} type [phone|num] - 类型
		 */
		formItemFocus(type) {
			this.isFocus=true
			switch (type) {
				case 'phone':
					this.phoneFocusStatus = true;
					break;
				case 'num':
					this.numFocusStatus = true;
					break;
				default:
					break;
			}
		},
		/**
		 * 失去焦点触发
		 * @param {String} type [phone|num] - 类型
		 */
		formItemBlur(type) {
			this.isFocus=false;
			switch (type) {
				case 'phone':
					this.phoneFocusStatus = false;
					break;
				case 'num':
					this.numFocusStatus = false;
					break;
				default:
					break;
			}
		},
		clo: function() {
		    this.passIn = false;
		    this.$refs['number'].close();
		    this.numberList.length = 0;
		},
		onChange(e) {
		    if (e.show == false) {
		        this.passIn = false;
		    }
		    this.numberList.length = 0;
		},
		onDelete() {
		    this.numberList.pop();
		},
		onConfirm() {
		    if (this.numberList.length != 6) {
		        uni.showToast({
		            title: '请输入六位交易密码！',
		            duration: 2000,
		            icon: 'none'
		        });
		    }
		},
		onInput(val) {
		    var that = this;
		    that.numberList.push(val);
		    that.password = that.numberList.join().replace(/,/g, '');
			console.log(that.numVal)
		    if (that.numberList.length >= that.length) {
		        uni.request({
		            url:this.url + 'cloudbuys/',
		            method: 'POST',
		            data: {
						cloudid:that.data,
						number:that.numVal,
						mobile:that.phoneVal,
						way:'1',
		                password: that.password
		            },
		            header: {
		                Authorization: 'JWT' + ' ' + that.global_.token
		            },
		            success(res) {
						console.log(that.data)
						console.log(that.numVal)
						console.log(that.phoneVal)
						console.log(that.password)
						console.log(res)
		                if (res.statusCode == 400) {
		                    that.$refs.wrong.flag = false;
		                    var n = res.data.data.err_num;
		                    var s = 5 - n;
		                    that.$refs.wrong.tip = '密码错误,剩余' + s + '次机会';
		                }
						if(that.seere<that.numVal) {
							that.sher = true
						}
		                if (res.statusCode == 200) {
		                    that.passIn = false;
		                    that.$refs['number'].close();
		                    uni.showToast({
		                        title: '转让完成',
		                        duration: 3000
								
		                    });
							uni.redirectTo({
								url:"../payftz/payftz?ler=" + that.phoneVal + '&cserf=' + that.numVal
							})
		                }
		                if (res.statusCode == 303) {
							that.passIn = false;
							that.$refs['number'].close();
		                    uni.showToast({
		                        icon: 'none',
		                        title: '非绑定销售关系，不可转让'
		                    });
		                }
						if(res.statusCode == 302) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title:'不允许转让经销商，普通用户，投资人和客户',
								icon:'none'
							})
						}
						if(res.statusCode == 305) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title:'交易发生错误,请重新创建',
								icon:'none'
							})
						}
						if (res.statusCode == 278) {
							that.passIn = false;
							that.$refs['number'].close();
						    uni.showToast({
						        icon: 'none',
						        title: '该用户不存在'
						    });
						}
						if(res.statusCode == 204) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title:'不允许转让自己',
								icon:"none"
							})
						}
		                var page = getCurrentPages().pop();
		                if (page == undefined || page == null) return;
		            }
		        });
		        this.numberList.length = 0;
		    }
		},
		// 提交
		submitForm() {
			if (!isPhoneNum(this.phoneVal)) {
				this.phoneErrTip = '手机号不正确';
			} else {
				this.phoneErrTip = '';
			}
			if (this.numVal=='' || isNaN(this.numVal) || this.numVal>parseFloat(this.seere)) {
				this.numErrTip = '数量不可用';
			}else{
				this.numErrTip = '';
			}
			if (this.phoneErrTip || this.numVal=='' || isNaN(this.numVal) || this.numVal>parseFloat(this.seere)) {
				return;
			}
			var that = this;
			this.passIn = true;
			this.$refs['number'].open();
			this.onInput(val);
		},

	}
};
